<template>
  <div class="shop-info"  v-if="Object.keys(shopInfo).length !== 0">
    <div class="shop-head">
      <img :src="shopInfo.log" alt="">
      <span>{{shopInfo.name}}</span>
    </div>
    <div class="shop-middle">
      <div class="shop-left">
        <div class="sell">
          <span>{{shopInfo.sells | sellCountFilter}}</span>
          <p>总销量</p>
        </div>
        <div class="goodsCount">
          <span>{{shopInfo.goodsCount}}</span>
          <p>全部宝贝</p>
        </div>
      </div>
      <div class="shop-right">
        <table>
          <tr v-for="(item,index) in shopInfo.score" :key="index">
            <td>{{item.name}}</td>
            <td class="score" :class="{'score-better': item.isBetter}">{{item.score}}</td>
            <td>
              <span :class="{'better-more':item.isBetter}" class="better">{{item.isBetter ? "高" : "低"}}</span>
            </td>
          </tr>
        </table>
      </div>
    </div>
    <div class="shop-bottom">
      <a>进店逛逛</a>
    </div>
  </div>
</template>
<script>
export default {
  name: 'DetailShopInfo',
  components: {},
  data() {
    return {
    }
  },
  props: {
    shopInfo: {
      type: Object,
      default(){
        return {}
      }
    }
  },
  filters: {
    sellCountFilter: function(value) {
      if(value < 10000) return value
      return (value/10000).toFixed(2) + '万'
    }
  }
}
</script>
<style scoped>
.shop-info {
  border-bottom: 5px solid #eee;
}

.shop-head {
  display: flex;
  align-items: center;
  padding: 20px 5px;
}
.shop-head img {
  width: 50px;
  border-radius: 50%;
  border: 1px solid #ccc;
}

.shop-head span {
  padding-left: 5px;
  font-size: 14px;
}

.shop-middle {
  display: flex;
  align-items: center;
}

.shop-middle>div {
  flex: 1;
}

.shop-left {
  display: flex;
  text-align: center;
  justify-content: space-evenly;
  border-right: 1px solid #ccc;
}
.shop-left>:first-child {
  margin-right: 20px;
}
.sell {
  margin-left: 20px;
}
.sell p,
.goodsCount p {
  padding-top: 8px;
  font-size: 14px;
}
.shop-right table {
  margin-left: 30px;
}
.shop-right td {
  padding: 5px;
  font-size: 14px;

}

.shop-right tr td:nth-child(3) {
    color: #fff;
}
.better {
  background-color: green;
}
.better-more {
  background-color: red;
}
.score {
  color: green;
}
.score-better {
  color: red;
}

.shop-bottom {
  text-align: center;
  font-size: 14px;
  padding: 15px 0;
}
.shop-bottom a {
  background-color: #ccc;
  border-radius: 5px;
  padding: 5px 25px;
}
</style>