<template>
  <div id="detail">
    <detail-nav-bar @titleClick="titleClick" ref="nav"></detail-nav-bar>
    <scroll class="detail-scroll" ref="scroll" :probe-type="3" @scroll="contenScroll">
      <detail-swiper :top-images="topImages"></detail-swiper>
      <detail-base-info :goods="goods"></detail-base-info>
      <detail-shop-info :shop-info="shopInfo" />
      <detail-goods-info :goods-info="goodsInfo" @goodsImgLoad="goodsImgLoad"/>
      <detail-param-info :param-info="paramInfo" ref="param"/>
      <detail-comment-info :comment-info="commentInfo" ref="comment" />
      <goods-list :goods-list="recommends" ref="goodsList" />
    </scroll>
    <back-top @click.native="backTopClick" v-show="isBackTopShow" />
    <detail-bottom-nav @addToCart="addToCart"></detail-bottom-nav>
    <toast :is-show="isShow" :message="messages"   />
  </div>
</template>
<script>
import DetailNavBar from './childComps/DetailNavBar'
import DetailSwiper from './childComps/DetailSwiper'
import DetailBaseInfo from './childComps/DetailBaseInfo'
import DetailShopInfo from './childComps/DetailShopInfo'
import DetailGoodsInfo from  './childComps/DetailGoodsInfo'
import DetailParamInfo from './childComps/DetailParamInfo'
import DetailCommentInfo from './childComps/DetailCommentInfo'
import DetailBottomNav from './childComps/DetailBottomNav'

import GoodsList from 'components/content/goods/GoodsList'
import Scroll from 'components/common/scroll/Scroll'
import Toast from 'components/common/toast/Toast'

import { getDetailData,getRecommend,Goods,Shop,Params } from 'network/detail'

import { itemImgListener,backTopShow } from 'commons/mixin'
import { debounce } from 'commons/utils'

import {mapActions} from 'vuex'

export default {
  name: 'Detail',
  components: {
    DetailNavBar,
    DetailSwiper,
    DetailBaseInfo,
    DetailShopInfo,
    DetailGoodsInfo,
    DetailParamInfo,
    DetailCommentInfo,
    GoodsList,
    DetailBottomNav,
    Scroll,
    Toast
  },
  data() {
    return {
      topImages: [],
      goods: {},
      shopInfo: {},
      goodsInfo: {},
      paramInfo: {},
      commentInfo: [],
      recommends:[],
      titleThemeY: [],
      getThemeY: null,
      currentIndex: null,
      id: null,
      messages: '',
      isShow: false,
      toast: null
    }
  },
  mixins: [itemImgListener,backTopShow],
  created(){
    this.id = this.$route.query.id
    getDetailData(this.id)
    .then(res => {
      // 1.获取顶部轮播图数据
      const data = res.result
      this.topImages = data.itemInfo.topImages
      // 2.获取商品基本信息数据
      this.goods = new Goods(data.itemInfo, data.columns, data.shopInfo.services)
      // 3.获取店铺信息
      this.shopInfo = new Shop(data.shopInfo)
      // 4.获取商品信息数据
      this.goodsInfo = data.detailInfo
      // 5.获取参数信息数据
      this.paramInfo = new Params(data.itemParams.info,data.itemParams.rule)
      // 6.获取评论数据
      if(data.rate.list.length){
        this.commentInfo = data.rate.list
      }
    })
    // 获取推荐商品数据
    getRecommend()
    .then(res => {
      this.recommends = res.data.list
    })
    // 点击标题跳到对应位置,给getThemeY赋值
    this.getThemeY = debounce(() => {
      this.titleThemeY =[]
      this.titleThemeY.push(0)
      this.titleThemeY.push(this.$refs.param.$el.offsetTop)
      this.titleThemeY.push(this.$refs.comment.$el.offsetTop)
      this.titleThemeY.push(this.$refs.goodsList.$el.offsetTop)
    },600)
  },
  methods: {
    goodsImgLoad(){
      this.$refs.scroll.Refresh()
      this.getThemeY()
    },
    titleClick(index){
      this.$refs.scroll.ScrollTo(0, -this.titleThemeY[index])
    },
    contenScroll(position){
      const positionY = -position.y
      let length = this.titleThemeY.length
      for(let i = 0; i < length; i++){
        if(this.currentIndex !== i && ((i < length-1 && positionY >= this.titleThemeY[i] && positionY < this.titleThemeY[i+1]
          ) || (i === length-1 && positionY >= this.titleThemeY[i]))){
          this.currentIndex = i
          this.$refs.nav.currentIndex = this.currentIndex
        }
      }
      this.isbacktopShow(position)
    },
    addToCart(){      
      const product = {}
      product.image = this.topImages[0]
      product.title = this.goods.title
      product.desc = this.goods.desc
      product.price = this.goods.realPrice
      product.iid = this.id

      this.$store.dispatch('addCart',product)
      .then(res => {
        this.$toast.toastShow(res,1000)
      })
    },
   
  },
  destroyed(){
    this.$bus.$off("itemImgLoad", this.itemImgListener)
  }
}
</script>
<style scoped>
#detail {
  height: 100vh;
}
.detail-scroll {
  height: calc(100% - 44px - 44px);
  position: relative;
  top: 0;
  z-index: 9;

  background-color: #fff;
}
</style>