<template>
  <div class="goods-info clear-fix" v-if="Object.keys(goodsInfo).length !== 0">
    <span class="left"></span>
    <p>{{goodsInfo.desc}}</p>
    <span class="right"></span>
    <p class="key">{{goodsInfo.detailImage[0].key}}</p>
    <div class="list">
      <img v-lazy="item" v-for="(item,index) in goodsInfo.detailImage[0].list" 
            :key="index" @load="imgLoad">
    </div>
  </div>
</template>
<script>
export default {
  name: 'DetailGoodsInfo',
  components: {},
  data() {
    return {
      count: 0,
      imgLength: 0
    }
  },
  props: {
    goodsInfo: {
      type: Object,
      default(){
        return {}
      }
    }
  },
  methods: {
    imgLoad(){
      if(++this.count === this.imgLength){
        this.$emit('goodsImgLoad')
      }
    }
  },
  watch: {
    goodsInfo(){
      this.imgLength = this.goodsInfo.detailImage[0].list.length   
    }
  }
}
</script>
<style scoped>
.goods-info {
  padding: 10px;
  font-size: 14px;
  color: #333;
  border-bottom: 5px solid #eee;
}
.goods-info p {
  padding: 10px 0;
}
.goods-info span {
  width: 100px;
  height: 1px;
  background-color: #333;
  position: relative;
}

.left::before,
.right::after {
  content: "";
  width: 5px;
  height: 5px;
  background-color: #333;

  position: absolute;
  bottom: 0;
}

.right::after {
  right: 0;
}

.list img {
  width: 100%;
}
</style>