<template>
    <nav-bar class="detail-nav">
      <template #left>
        <div class="back" @click="back">
          <img src="~assets/img/common/back.svg" style="max-width:60px;max-height:44px;">        
        </div>
      </template>
      <template #center >
        <div class="bar">
          <div v-for="(item,index) in title" :key="index" class="item"
               :class="{active: currentIndex === index}" @click="itemClick(index)">{{item}}</div>
        </div>
      </template>
    </nav-bar>
</template>
<script>
import NavBar from 'components/common/navBar/NavBar'

export default {
  name: 'DetailNavBar',
  components: {
    NavBar
  },
  data() {
    return {
      title: ['商品','参数','评论','推荐'],
      currentIndex: 0
    }
  },
  methods: {
    itemClick(index){
      this.currentIndex = index
      this.$emit('titleClick',index)
    },
    back(){
      this.$router.back()
    }
  }
}
</script>
<style scoped>
.detail-nav {
  background-color: #fff;

  position: relative;
  z-index: 999;
}
.bar {
  display: flex;
}
.item {
  flex: 1;
}

.back {
  position: relative;
  height: 100%;
}
.back img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
} 

.active {
  color: var(--color-high-text)
}
</style>